/* Color Variables */
:root {
  --node-unvisited-color: #cc4112;
  --node-visited-color: #006b0c;
  --node-finish-color: red;
  --node-start-color: green;
  --node-outline-color: rgb(175, 216, 248);
  --mountain-texture-url: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><polygon points="10,70 25,30 40,70" fill="%238B4513" opacity="0.9"/><polygon points="35,70 50,20 65,70" fill="%23A0522D" opacity="0.9"/><polygon points="60,70 75,35 90,70" fill="%238B4513" opacity="0.9"/><circle cx="20" cy="65" r="2" fill="%23654321" opacity="0.6"/><circle cx="30" cy="68" r="1.5" fill="%23654321" opacity="0.6"/><circle cx="45" cy="60" r="2" fill="%23654321" opacity="0.6"/><circle cx="55" cy="65" r="1.5" fill="%23654321" opacity="0.6"/><circle cx="70" cy="62" r="2" fill="%23654321" opacity="0.6"/><circle cx="80" cy="67" r="1.5" fill="%23654321" opacity="0.6"/><polygon points="48,20 52,20 50,25" fill="%23FFFFFF" opacity="0.8"/></svg>');
}

.node {
  width: 25px;
  height: 25px;
  outline: 1px solid var(--node-outline-color);
  display: inline-block;
  background-color: var(--node-unvisited-color);
}

.node-finish {
  background-color: var(--node-finish-color) !important;
}

.node-start {
  background-color: var(--node-start-color) !important;
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 107, 12, 0.3);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(0, 107, 12, 0.6);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 107, 12, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: var(--node-visited-color);
  }
}

.node-wall {
  background-image: var(--mountain-texture-url);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: #8B7355;
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(255, 254, 106);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 254, 106);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 254, 106);
  }
}
